<template>
    <div class='title'>
    <i class="cubeic-back" @click='fn'></i>    {{title}}
    </div>
</template>

<script>
    export default {
        computed: {
            title() {
                return this.$route.meta.title 
            }
        },
        methods: {
            fn() {
                this.$router.back();
            }
        },
    }
</script>

<style lang="less" scoped>
    .title{
        position: fixed;
        top:0;
        left:0;
        height:1rem;
        width:100%;
        text-align: center;
        line-height: 1rem;
        background: #14B9C8;
        font-size: 0.4rem;
        color:#fff;
        z-index: 99;
    }
    .cubeic-back{
        float: left;
        position: relative;
        left: 0.3rem;
    }
</style>