export function add (state){
    state.count++
}
export function remove(state){
    state.count--
}
export function changeBanner(state,options){
    //改变state中的bannerList
    state.bannerList = options
}
export function changeList(state,options){
    //重置state中的列表页数据
    state.listData = options
    state.isGetList = true
}
export function del(state,options){
    state.listData = state.listData.filter((item)=>item.bookId!==options.bookId)
}
export function delC(state,options){
    state.collectData = state.collectData.filter((item)=>item.bookId!==options.bookId)
}
export function collect(state,options){
    //再添加之前要先去判断有没有，有的话就不用再去添加了
    if(state.collectData.some((item)=>item.bookId == options.bookId))return
    state.collectData.unshift(options)
}
export function addList(state,options){
    //再添加之前要先去判断有没有，有的话就不用再去添加了
    state.listData.unshift(options)
}

//这是判断是否是本人的方法
export function myaccount(state,options){
   
    
    state.account.push(options)
     
}