
export default [
    {
        path: '/login',
        name: '/login',
        component: () => import('../views/login/login')
    },
    {
        path: '/',
        redirect: '/login',
        component: () => import('../views/views.vue'),
        children: [
            {
                path: '/home',
                name: '/home',
                component: () => import('../views/home'),
                meta: {
                    title: '首页'
                }
            },
            {
                path: '/list',
                name: '/list',
                component: () => import('../views/list'),
                meta: {
                    title: '书架'
                }
            },
            {
                path: '/person',
                name: '/person',
                component: () => import('../views/person'),
                meta: {
                    title: '我的'
                }
            },
            {
                path: '/collect',
                name: '/collect',
                component: () => import('../views/collect'),
                meta: {
                    title: '购物车'
                }
            },
        ]
    },
]